'use client';

import {useEffect, useState} from "react";
import Image from "next/image";
import Splash from "../app/view/splash";
import HomePage from "../app/view/home";
import AccountPage from "../app/view/account";
import StudioPage from "../app/view/studio";
import Drawer from "../app/view/layout-components/drawer";
import CarouselWrapper from "./view/layout-components/carousel-wrapper";
import Game1 from "../app/view/game-components/drawing-game";
import Game from "../app/view/game";
import { PageProvider, usePage } from "../app/view/page-contextt";  

function PageContent() {
  // const [showSplash, setShowSplash] = useState(true);
  const { currentPage } = usePage();
  const [showSplash, setShowSplash] = useState(true);
  // const [currentPage, setCurrentPage] = useState('home');

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowSplash(false);
    }, 2000);
    return () => clearTimeout(timer); 
  }, []);

  const renderPage = () => {
    switch(currentPage){
      case 'home' : return <HomePage/>;
      case 'account' : return <AccountPage/>;
      case 'studio' : return <StudioPage/>;
      case 'game' : return <Game/>;
      default: return <HomePage/>;
    }
  };

  return showSplash?(
    <Splash/>
  ) : (
    <div>
      {renderPage()}
      <Drawer setPage={setCurrentPage}/>
    </div>
  );
}

export default function Page() {
  return (
    <PageProvider>
      <PageContent/>
    </PageProvider>
  );
}